import axios from 'axios';
import { GET_ERRORS, SET_CURRENT_USER, GET_RESPONSE_TYPE, GET_REQUEST_INFO } from './types';
import setAuthToken from '../setAuthToken';
import jwt_decode from 'jwt-decode';

export const registerUser = (user) => dispatch => {
  dispatch(initResponseError());
  dispatch(requestInfo(user));
  axios.post('/api/users/register', user)
    .then(res => {
      dispatch(setResponseType('register'));
    })
    .catch(err => {
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      });
    });
}

export const inviteMember = (data, history) => dispatch => {
  dispatch(initResponseError());
  axios.post('/api/users/invite', data)
    .then(res => {
      dispatch(storeToken(res, 'invite'));
      history.push('/');
    })
    .catch(err => {
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      });
    });
}

