export const loginUser = (user, history) => dispatch => {
  dispatch(initResponseError());
  dispatch(requestInfo(user));
  axios.post('/api/users/login', user)
    .then(res => {
      dispatch(storeToken(res, 'login'));
      history.push('/');
    })
    .catch(err => {
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      });
    });
}

export const storeToken = (res, res_type) => {
  const { token } = res.data;
  localStorage.setItem('jwtToken', token);
  setAuthToken(token);
  setResponseType(res_type);
  const decoded = jwt_decode(token);
  return setCurrentUser(decoded);
}

export const initResponseError = () => {
  return {
    type: GET_ERRORS,
    payload: {}
  }
}

export const requestInfo = info => {
  return {
    type: GET_REQUEST_INFO,
    payload: info
  }
}

export const setResponseType = type => {
  return {
    type: GET_RESPONSE_TYPE,
    payload: type
  }
}

export const setCurrentUser = decoded => {
  return {
    type: SET_CURRENT_USER,
    payload: decoded
  }
}

export const logoutUser = () => dispatch => {
  localStorage.removeItem('jwtToken');
  setAuthToken(false);
  dispatch(setCurrentUser({}));
}